"use client";

import React, { useState } from "react";

const stores = ["Arkan Plaza", "Arabella Plaza", "City Stars", "Warehouse"];

interface TransferEntry {
  code: string;
  quantity: number;
  fromStore: string;
  toStore: string;
}

export default function HomePage() {
  const [name, setName] = useState("");
  const [code, setCode] = useState("");
  const [quantity, setQuantity] = useState<number | "">("");
  const [fromStore, setFromStore] = useState(stores[0]);
  const [toStore, setToStore] = useState(stores[1]);
  const [entries, setEntries] = useState<TransferEntry[]>([]);

  const addEntry = () => {
    if (!name.trim()) {
      alert("Please enter your name.");
      return;
    }
    if (!code.trim() || !quantity || quantity <= 0 || fromStore === toStore) {
      alert("Please enter valid code, quantity and different stores.");
      return;
    }
    setEntries((prev) => [...prev, { code, quantity: Number(quantity), fromStore, toStore }]);
    setCode("");
    setQuantity("");
  };

  const clearEntries = () => {
    setEntries([]);
  };

  const submitEntries = () => {
    if (entries.length === 0) {
      alert("No entries to submit.");
      return;
    }
    // Save entries with name to sessionStorage
    const existingData = sessionStorage.getItem("transferData");
    const existingEntries: any[] = existingData ? JSON.parse(existingData) : [];
    const newEntries = entries.map((entry) => ({ ...entry, name }));
    const allEntries = [...existingEntries, ...newEntries];
    sessionStorage.setItem("transferData", JSON.stringify(allEntries));
    alert("Entries submitted successfully.");
    setEntries([]);
    setName("");
  };

  return (
    <main className="min-h-screen bg-white text-black flex flex-col items-center p-6 font-sans">
      <h1 className="text-3xl font-bold mb-6">Stock Transfer</h1>
      <div className="w-full max-w-md space-y-4">
        <div>
          <label htmlFor="name" className="block mb-1 font-semibold">
            Your Name
          </label>
          <input
            id="name"
            type="text"
            value={name}
            onChange={(e) => setName(e.target.value)}
            className="w-full border border-black rounded px-3 py-2"
            placeholder="Enter your name"
          />
        </div>
        <div>
          <label htmlFor="code" className="block mb-1 font-semibold">
            Code
          </label>
          <input
            id="code"
            type="text"
            value={code}
            onChange={(e) => setCode(e.target.value)}
            className="w-full border border-black rounded px-3 py-2"
            placeholder="Enter code"
          />
        </div>
        <div>
          <label htmlFor="quantity" className="block mb-1 font-semibold">
            Quantity
          </label>
          <input
            id="quantity"
            type="number"
            min={1}
            value={quantity}
            onChange={(e) => setQuantity(e.target.value === "" ? "" : Number(e.target.value))}
            className="w-full border border-black rounded px-3 py-2"
            placeholder="Enter quantity"
          />
        </div>
        <div className="flex space-x-4">
          <div className="flex-1">
            <label htmlFor="fromStore" className="block mb-1 font-semibold">
              From Store
            </label>
            <select
              id="fromStore"
              value={fromStore}
              onChange={(e) => setFromStore(e.target.value)}
              className="w-full border border-black rounded px-3 py-2"
            >
              {stores.map((store) => (
                <option key={store} value={store}>
                  {store}
                </option>
              ))}
            </select>
          </div>
          <div className="flex-1">
            <label htmlFor="toStore" className="block mb-1 font-semibold">
              To Store
            </label>
            <select
              id="toStore"
              value={toStore}
              onChange={(e) => setToStore(e.target.value)}
              className="w-full border border-black rounded px-3 py-2"
            >
              {stores.map((store) => (
                <option key={store} value={store}>
                  {store}
                </option>
              ))}
            </select>
          </div>
        </div>
        <button
          onClick={addEntry}
          className="w-full bg-black text-white py-2 rounded font-semibold hover:bg-gray-800 transition"
        >
          Add Entry
        </button>
        {entries.length > 0 && (
          <div className="mt-6">
            <h2 className="text-xl font-semibold mb-2">Entries</h2>
            <ul className="border border-black rounded max-h-48 overflow-y-auto">
              {entries.map((entry, idx) => (
                <li
                  key={idx}
                  className="border-b last:border-b-0 border-black px-3 py-2 flex justify-between"
                >
                  <span>
                    {entry.code} - Qty: {entry.quantity} - From: {entry.fromStore} - To: {entry.toStore}
                  </span>
                </li>
              ))}
            </ul>
            <button
              onClick={clearEntries}
              className="mt-3 w-full bg-red-600 text-white py-2 rounded font-semibold hover:bg-red-700 transition"
            >
              Clear Entries
            </button>
            <button
              onClick={submitEntries}
              className="mt-3 w-full bg-green-700 text-white py-2 rounded font-semibold hover:bg-green-800 transition"
            >
              Submit Entries
            </button>
          </div>
        )}
      </div>
    </main>
  );
}
